.AnswerArea {
    display: flex;
    padding: 0.5em;
    flex-flow: row wrap;
    justify-content: center;

    button {
        flex: 0 0 80%;
    }
}

@media (min-width: 650px) and (max-width: 800px) {
    .AnswerArea {
        width: 85%;
        box-sizing: border-box;
        margin: 0 auto;
    }

    .AnswerArea button {
        font-size: 1.7em;
    }
}

@media (min-width: 801px) {
    .AnswerArea {
        position: inherit;
        display: flex;
        flex-wrap: wrap;
        text-align: center;
        justify-content: center;
    }
}

