.Spinner_loader__35_IE,
.Spinner_loader__35_IE:before,
.Spinner_loader__35_IE:after {
    border-radius: 50%;
}
.Spinner_loader__35_IE {
    color: #684c2f;
    font-size: 11px;
    text-indent: -99999em;
    margin: 55px auto;
    position: relative;
    width: 10em;
    height: 10em;
    box-shadow: inset 0 0 0 1em;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
}
.Spinner_loader__35_IE:before,
.Spinner_loader__35_IE:after {
    position: absolute;
    content: '';
}
.Spinner_loader__35_IE:before {
    width: 5.2em;
    height: 10.2em;
    background: #f4e7c7;
    border-radius: 10.2em 0 0 10.2em;
    top: -0.1em;
    left: -0.1em;
    -webkit-transform-origin: 5.2em 5.1em;
    transform-origin: 5.2em 5.1em;
    -webkit-animation: Spinner_load2__aixSK 2s infinite ease 1.5s;
    animation: Spinner_load2__aixSK 2s infinite ease 1.5s;
}
.Spinner_loader__35_IE:after {
    width: 5.2em;
    height: 10.2em;
    background: #f4e7c7;
    border-radius: 0 10.2em 10.2em 0;
    top: -0.1em;
    left: 5.1em;
    -webkit-transform-origin: 0px 5.1em;
    transform-origin: 0px 5.1em;
    -webkit-animation: Spinner_load2__aixSK 2s infinite ease;
    animation: Spinner_load2__aixSK 2s infinite ease;
}
@-webkit-keyframes Spinner_load2__aixSK {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}
@keyframes Spinner_load2__aixSK {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

